'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name isEqual
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/complex/base/assert/is-equal}
*/
import isEqual from './../../../base/assert/is-equal';
setReadOnly( ns, 'isEqual', isEqual );

/**
* @name isEqualf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/complex/base/assert/is-equalf}
*/
import isEqualf from './../../../base/assert/is-equalf';
setReadOnly( ns, 'isEqualf', isEqualf );

/**
* @name isNotEqual
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/complex/base/assert/is-not-equal}
*/
import isNotEqual from './../../../base/assert/is-not-equal';
setReadOnly( ns, 'isNotEqual', isNotEqual );

/**
* @name isNotEqualf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/complex/base/assert/is-not-equalf}
*/
import isNotEqualf from './../../../base/assert/is-not-equalf';
setReadOnly( ns, 'isNotEqualf', isNotEqualf );

